import React, { Component } from 'react'
import { Button } from "antd"
import MobileView from "@/view/StoryView/MobileView/index"
import * as api from '@/axios/api'

export default class H5page extends Component {

    constructor(props) {
        super(props);
        this.state = {
            storyProps: {}
        }
    }

    UNSAFE_componentWillMount = async () => {
        let id = window.location.href.split("#/H5/")[1]
        let response = await api.fetchStory(id);
        this.setState({
            storyProps: response.data
        })

        // localStorage.facts= "your Data";
        // localStorage.data= "your Data";
        // console.log(localStorage)
        // let _storyProps = JSON.parse(localStorage.getItem("storyProps"))
        // let storyProps = _.cloneDeep(_storyProps)
        // console.log(storyProps)

        // set the data in state and use it through the component
        // localStorage.removeItem("facts");
        // removing the data from localStorage.  Since if user clicks for another invoice it overrides this data
    }
    publish = () => {
        window.close()
    }

    render() {
        const { initDone, intl } = this.state.storyProps
        let currentLan = intl && intl.options.currentLocale
        return (
            <>
                <Button style={{ position: 'absolute', right: '30px', top: "30px" }} onClick={this.publish}>{initDone && intl && intl.options.locales[currentLan]["publish"]}</Button>
                <MobileView {...this.state.storyProps} forPublish={true} />
            </>

        )
    }
}
